.footer {
    padding-top: 56px;
    padding-bottom: 56px;
} 

.footer-container {
  display: flex;
  justify-content: space-between;
}

.footer-text {
  font-weight: 600;
  font-size: 11px;
  line-height: 1.36;
  letter-spacing: 0.2em;
  color: $third-text-color;
}

.footer-list {
    display: flex;
}

.footer-item {
    &:not(:last-child) {
        margin-right: 40px;
    }
}

.footer-link {
  font-weight: 600;
  font-size: 11px;
  line-height: 1.36;
  letter-spacing: 0.2em;
  color: $secondary-text-color;
}
